@keyframes rotate-arrow {
  from {
    transform: translate(-100%, -50%)rotate(90deg);
  }

  to {
    transform: translate(-100%, -50%)rotate(440deg);
  }
}

.watch {
  border: 3px dotted #000;
  border-radius: 50%;
  width: 400px;
  height: 400px;
  margin: 25% auto;
  position: relative;
}

.watch__center {
  background-color: orange;
  border-radius: 50%;
  width: 8%;
  height: 8%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.watch__minute {
  transform-origin: 100%;
  background-color: #00f;
  width: 25%;
  height: 5%;
  animation: 60s linear infinite rotate-arrow;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-100%, -50%)rotate(90deg);
}

.watch__second {
  transform-origin: 100%;
  z-index: -1;
  background-color: #008001;
  width: 50%;
  height: 2.5%;
  animation: 5s linear infinite rotate-arrow;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-100%, -50%)rotate(90deg);
}

html, body {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100%;
  margin: 0;
  display: flex;
}

h1 {
  text-align: center;
}

